import Link from 'next/link';
import amino from '../../img/disease/amino.svg';
const disease = () => (
	<div className="diseasecomponent">
		<h2>
			<strong>โรคและวิธีการรักษา</strong>
		</h2>
		<div className="col-xs-12 col-sm-12 col-md-12 col-lg-4">
			<div className="container-fluid">
				<div className="row">
					<div className="col-xs-12 col-sm-6 col-md-6 col-lg-12">
						<div id="disease-content" className="border pl-1 pr-1 pt-1">
							<div className="row">
								<div className="col-xs-4 col-sm-3 col-md-3 col-lg-2">
									<img src={amino} width="45px" />
								</div>
								<div className="col-xs-8 col-sm-9 col-md-9 col-lg-10">
									<Link href="/Disease/amino-acid">
										<h5 className="pt-2 pl-2 pb-2">กรดอะมิโน</h5>
									</Link>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
			<div className="mt-5">
				<div className="diseasecomponent">
					<div className="container-fluid">
						<div className="row">
							<div id="diseaseHeader" className="col-sm-12 col-md-12 col-lg-12">
								<h2>
									<strong>กรดอะมิโน</strong>
								</h2>
							</div>

							<div id="disease-sub" className="col-sm-12 col-md-12 col-lg-12 pt-3 pl-4">
								<div className="row">
									<h4 className="pt-2">
										กรดอะมิโน เรียกได้ว่าเป็นเสมือนก้อนอิฐที่ใช้สำหรับสร้างชีวิตเลยดีเดียว
									</h4>
									<p className="pt-2">
										ร่างกายสามารถได้รับกรดอะมิโนในปริมาณเพียงพอกับที่ต้องการได้จากการรับประทานอาหารที่
										ประกอบด้วยกรดอะมิโนเหล่านั้น กรดอะมิโน เป็นสารที่ใช้ในการสร้างโปรตีน
										สามารถพบได้ทั่ว ไปในร่างกาย กรดอะมิโนเหล่านี้
										เรียกได้ว่าเป็นก้อนอิฐแห่งชีวิตเลยก็ว่าได้ กรดอะมิโนมีความ
										จำเป็นในการสร้างเอนไซม์ หรือน้ำย่อยต่าง ๆ ร่วมถึงฮอร์โมน และสารสื่อประสาท
										นอกจากนี้ ยังเกี่ยวข้องกับกระบวนการทางเมตะบอลิสม
										หรือกระบวนการย่อยสลายสารอาหารภายในเซลล์ ทั่วร่างกาย
										คุณจะได้รับกรดอะมิโนนี้จากการกินอาหาร
									</p>
									<p className="pt-2">
										หลังจากที่ร่างกายของคุณย่อยโปรตีนแล้ว โปรตีนเหล่านั้นจะอยู่ในรูปของกรดอะมิโน
										ซึ่งจะนำไป ใช้ในกระบวนการต่าง ๆ ของร่างกาย ดังนี้
									</p>

									<ul className="col-sm-12 col-md-12 col-lg-12 ">
										<li>ย่อยสลายอาหาร</li>
										<li>สร้างเนื้อเยื่อ และซ่อมแซมส่วนที่สึกหรอ</li>
										<li>เป็นแหล่งพลังงาน</li>
										<li>ช่วยให้การทำงานของระบบต่าง ๆ ในร่างกายเป็นไปได้อย่างปกติ</li>
									</ul>
									<p>SSSSS</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
);
export default disease;

